@import"https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap";.profile-page{min-height:90vh;background:linear-gradient(120deg, #181920 0%, #e9e6ff 60%, #fffbe9 100%);display:flex;justify-content:center;align-items:flex-start;padding:32px 0 56px 0;font-family:"Inter","Segoe UI",Arial,sans-serif;position:relative;overflow:hidden}.profile-bg-shape{position:absolute;z-index:0;border-radius:60%;filter:blur(48px);opacity:.45;pointer-events:none;transition:filter .4s ease}.profile-bg-shape.shape-1{width:340px;height:250px;top:-40px;left:-80px;background:radial-gradient(circle at 40% 60%, #bc6ff1 70%, #61dfa4 100%);animation:floatShape1 6s infinite alternate cubic-bezier(0.77, 0.17, 0.37, 1.16)}.profile-bg-shape.shape-2{width:280px;height:210px;bottom:-60px;right:-60px;background:radial-gradient(circle at 60% 40%, #ff9950 60%, #ffc73c 100%);animation:floatShape2 7s infinite alternate cubic-bezier(0.6, 1.2, 0.25, 0.87)}.profile-bg-shape.shape-3{width:180px;height:150px;left:70%;top:66%;background:radial-gradient(circle, #a259f7 70%, #61dfa4 100%);opacity:.28;animation:floatShape3 8s infinite alternate cubic-bezier(0.55, 1, 0.6, 1.2)}@keyframes floatShape1{to{top:30px;left:-50px;filter:blur(32px)}}@keyframes floatShape2{to{bottom:-20px;right:-30px;filter:blur(38px)}}@keyframes floatShape3{to{left:68%;top:63%;filter:blur(22px)}}.profile-container{background:linear-gradient(120deg, #fff 60%, #f2eaff 100%);border-radius:32px;box-shadow:0 6px 36px rgba(215,170,255,.33),0 0 0 5px rgba(186,255,237,.2);border:2.5px solid rgba(0,0,0,0);position:relative;max-width:420px;width:100%;margin:0 auto;padding:42px 32px 32px 32px;animation:fadeInProfile .7s cubic-bezier(0.4, 1.6, 0.6, 1.1)}.profile-container::before{content:"";position:absolute;inset:0;z-index:0;border-radius:inherit;padding:2.7px;background:linear-gradient(90deg, #61dfa4 0%, #bc6ff1 60%, #ff9950 100%);pointer-events:none;opacity:.82;filter:blur(2.5px)}.profile-container .divider{margin:30px auto 18px auto;width:75%;height:4px;border-radius:5px;background:linear-gradient(90deg, #61dfa4 0%, #bc6ff1 60%, #ff9950 100%);opacity:.18;border:none}.profile-container .section-heading{font-size:1.125rem;color:#bc6ff1;font-weight:700;margin:0 0 12px 0;letter-spacing:.02em;display:flex;align-items:center;gap:6px}.profile-container .section-heading::before{content:"✨";font-size:1.15em}.profile-sticker{position:absolute;top:-36px;right:-26px;z-index:2;width:62px;height:62px;background:linear-gradient(120deg, #fffbe9 40%, #baffed 100%);border-radius:50%;box-shadow:0 4px 20px rgba(255,199,60,.13),0 1px 0 #fffbe9;display:flex;align-items:center;justify-content:center}.profile-sticker img,.profile-sticker svg{width:40px;height:40px;display:block;filter:drop-shadow(0 0 8px rgba(255, 184, 81, 0.6))}.profile-container>*{position:relative;z-index:1}.profile-container h1{font-size:1.5rem;font-weight:700;color:#7d58c2;margin-bottom:24px;text-align:center;letter-spacing:.015em}.success-message{background:linear-gradient(90deg, #a5ffce 40%, #ffea8a 100%);color:#296d30;border-radius:7px;padding:10px 14px;margin-bottom:18px;text-align:center;font-weight:600}.error-message{background:linear-gradient(90deg, #ffb8b8 60%, #ffe5e2 100%);color:#a83838;border-radius:7px;padding:10px 14px;margin-bottom:18px;text-align:center;font-weight:600}.profile-info{display:flex;flex-direction:column;align-items:center;gap:17px;animation:fadeInInfoCard .7s}.profile-info .profile-avatar{margin-bottom:10px}.profile-info .profile-avatar .avatar{width:110px;height:110px;border-radius:50%;object-fit:cover;background:linear-gradient(135deg, #a259f7 0%, #fff 40%, #ffa751 100%);border:3.5px solid #bc6ff1;box-shadow:0 4px 24px 0 rgba(255,206,138,.3333333333),0 1px 0 rgba(112,238,191,.4666666667);margin-bottom:4px;transition:filter .25s}.profile-info .profile-avatar .avatar:hover{filter:brightness(1.11) drop-shadow(0 0 10px rgba(84, 186, 92, 0.5333333333))}.profile-info .info-group{width:100%;display:flex;justify-content:space-between;align-items:center;border-bottom:1.4px dashed #e0d3f6;padding:8px 0;font-size:1.09rem;color:#50446c}.profile-info .info-group label{font-weight:600;color:#a259f7;min-width:110px}.profile-info .info-group p{font-weight:500;color:#319f59;margin:0;text-align:right;word-break:break-word;letter-spacing:.01em}.profile-info .edit-btn{margin-top:14px;padding:11px 28px;border-radius:9px;background:linear-gradient(90deg, #54ba5c 10%, #bc6ff1 100%);color:#fff;font-weight:600;border:none;box-shadow:0 2px 14px rgba(84,186,92,.1333333333);cursor:pointer;font-size:1.09rem;transition:background .23s,filter .25s}.profile-info .edit-btn:hover{background:linear-gradient(90deg, #bc6ff1 20%, #ff9950 90%);filter:brightness(1.09) drop-shadow(0 0 8px rgba(255, 184, 81, 0.3333333333))}.edit-form{margin-top:12px;display:flex;flex-direction:column;gap:12px}.edit-form .form-group{display:flex;flex-direction:column;align-items:flex-start;gap:4px;margin-right:30px}.edit-form .form-group label{font-weight:600;color:#7d58c2;margin-bottom:2px;font-size:1.01rem}.edit-form .form-group input{font-size:1.09rem;border-radius:7px;padding:10px 14px;border:2px solid #f4ebff;background:#fcf7ff;outline:none;transition:border .18s,box-shadow .2s;width:100%}.edit-form .form-group input:focus{border-color:#bc6ff1;box-shadow:0 2px 10px rgba(162,89,247,.3333333333)}.edit-form .form-actions{display:flex;gap:15px;margin-top:12px;justify-content:flex-end}.edit-form .form-actions .save-btn,.edit-form .form-actions .cancel-btn{padding:10px 26px;border-radius:8px;font-size:1rem;font-weight:600;border:none;cursor:pointer;transition:background .2s,color .2s}.edit-form .form-actions .save-btn{background:linear-gradient(90deg, #61dfa4 20%, #a259f7 100%);color:#fff;box-shadow:0 2px 8px rgba(97,223,164,.3333333333)}.edit-form .form-actions .save-btn:hover{background:linear-gradient(90deg, #ff9950 10%, #bc6ff1 90%);color:#fff;filter:brightness(1.1) drop-shadow(0 0 8px rgba(255, 153, 80, 0.4))}.edit-form .form-actions .cancel-btn{background:#fff;color:#a259f7;border:2px solid #a259f7}.edit-form .form-actions .cancel-btn:hover{background:#f8e1ff;color:#ff9950;border-color:#ff9950}.loader-container{min-height:70vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.loader-container p{color:#7d58c2;margin-top:18px;font-size:1.13rem}@keyframes fadeInInfoCard{0%{opacity:0;transform:translateY(18px)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeInProfile{0%{opacity:0;filter:blur(18px);transform:scale(0.97) translateY(36px)}100%{opacity:1;filter:blur(0);transform:scale(1) translateY(0)}}@media(max-width: 768px){.profile-container{max-width:99vw;padding:24px 16px 20px 16px;border-radius:16px}.profile-info .profile-avatar .avatar{width:84px;height:84px}}